.about-us
  &__container
    display: flex
    justify-content: center
    // flex-direction: column
    align-items: center
    align-content: stretch
    flex-wrap: wrap
    padding: 50px 0 50px 0
    max-width: 1280px
    @media(max-width: 1200px)
      // height: 675px
      padding: 30px 0 30px 0

      flex-direction: column
    @media(max-width: 1024px)
      display: flex
      flex-direction: column
      align-items: center
      flex-wrap: nowrap
  &__content
    display: flex
    justify-content: space-around
    flex-direction: column
    font-size: $mediumsize
    max-width: 588px
    min-height: 245px
    min-height: 0px
    padding: 0 2vw 0px 0vw
    @media (max-width: 10200x)
      padding: 0
      max-width: 642px
      width: auto
      height: 40px
      margin: 50px 0 0vw 0
    &-mob
      @media (max-width: 1200px)
        display: flex
        flex-direction: column
        width: 80vw
        max-width: 590px
        min-width: 290px
        min-height: 170px
        justify-content: space-between

    &-title
      // padding: 0px 0px 13px 140px
      font: 36px/48px Banny,
      height: 65px
      // padding: 20px 0 20px 0
      &::after
        @include after-plank(54px, 0px, 0px, -117px, 60px, 5.11px)
        @media (max-width: 500px)
          @include after-plank(44px, 0px, 0px, -117px, 60px, 5.11px)
    &-subtitle
      font: bold 18px/28px 'OpenSansBold', sans-serif
      font-weight: 700
      min-height: 50px
      padding: 20px 0 20px 0

      @media (max-width: 1200px)
        // margin: 0 0 20px 0
        padding: 20px 0 20px 0
    &-text
      font: 15px/24px OpenSans-Regular,sans-serif
      min-height: 90px
      // @media (max-width: 1200px)
      //   padding: 0 0 60px 0
    &-image
      display: flex
      justify-content: center
      flex-direction: column
      align-content: flex-start
      align-items: flex-end
      flex-wrap: wrap
      max-width: 600px
      width: -webkit-fill-available
      width: -moz-available
      align-content: last baseline
      @media(max-width: 630px)
        width: 60vw
      &::after
        @include after-frame(0px, 0px, 0px, 0px, 250px, 250px)
        @media(max-width: 630px)
          @include after-frame(0px, 0vw, 0px, 0px, 25vw, 25vw)
          border: 3vw solid #e8c300
      &-picture
        float: left
        width: 475px
        height: 475px
        // margin: 0px 120px 0 0
        @media(max-width: 630px)
          height: 47vw
          width: 47vw
