//- Base
@import '__reset', '__mixin', '__fonts', '__variables'
//- Partials
@import '../../views/partials/header/header'
@import '../../views/partials/footer/footer'

//- Components

@import '../../views/components/photo-galerie/photo-galerie'
@import '../../views/components/About us Section/About us Section'
@import '../../views/components/booking/booking'
@import '../../views/components/contact/contact'
@import '../../views/components/Delicious/Delicious'
@import '../../views/components/Private-Events/PrivateEvents'
@import '../../views/components/Specialties/Specialties'
@import '../../views/components/Team/Team'
@import '../../views/components/map/map'
@import '../../views/components/buttons/buttons'
@import '../../views/components/drop-menu/drop-menu'

html
  scroll-behavior: smooth
body
  background: $white
  text-align: -webkit-center
  text-align: -moz-center
  text-align: -ms-center
  margin: auto

a:focus-visible
  outline: 1px solid white
.text
  $parentText: &
  &--upper
    text-transform: uppercase
  &--white
    color: $white

.hidden
  visibility: hidden

@media (min-width: 320px)
  head
    width: 320px

@media (min-width: 480px)
  head
    width: 480px

@media (min-width: 700px)
  head
    width: 700px
.bold
  font-weight: bold

.hidden__for-1024px
  @media(max-width: 1024px)
    display: none
.visible__for-1024px
  @media(min-width: 1025px)
    display: none

.hidden__for-1200px
  @media(max-width: 1200px)
    display: none
.visible__for-1200px
  @media(min-width: 1201px)
    display: none
