.booking
  &__container
    max-width: 1440px
    display: block
    justify-content: center
    flex-direction: column
    align-content: stretch
    // @media(min-width: 1120px)
    //   height: 680px
    padding: 50px 0 45px 0

    // @media(max-width: 1120px)
    @media(max-width: 680px)
      padding: 30px 0 30px 0
  &__content
    display: flex
    // flex-direction: row
    justify-content: space-evenly
    align-items: center
    flex-wrap: wrap
    // @media(max-width: 1120px)
      // flex-direction: column
    &-title
      display: flex
      align-items: center
      flex-direction: column
      font: 36px/48px Banny,
      height: 81px
      &::after
        @include after-plank( 60px, 0, 0, 0px, 60px, 5.11px)
        @media(max-width: 680px )
          margin: 60px 0 0 0px
      @media(max-width: 390px)
        font-size: 12vw

    &-forms
      display: flex
      flex-direction: column
      // margin: 0 0px 0 10px
      &-form
        display: flex
        @media(max-width: 680px )
          flex-direction: column

        //   flex-direction: column
      &-input
        width: 277.5px
        height: 60px
        background: #E5E5E5
        border-radius: 5px
        margin: 5px
        border: none
        outline: none
        font: 15px/19px OpenSans-Regular,
        padding: 7px 15px 5px 18px
        @media(max-width: 680px )
          width: auto
          min-width: 80vw
          height: 75px
          border-radius: 15px
          font-size: 16px
        &:invalid
          border: 0.3px solid #dacaca00
        &:valid
          border: 0.5px solid yellow

    &-image
      display: flex
      justify-content: center
      align-content: center
      flex-direction: column
      align-content: flex-start
      align-items: flex-end
      flex-wrap: wrap
      max-width: 600px
      width: -webkit-fill-available
      width: -moz-available
      align-content: last baseline
      margin: 45px 0 45px 0

      @media(max-width: 1200px)
        margin: 30px 0 30px 0
      @media(max-width: 630px)
        width: 60vw
        margin: 30px 0 30px 0

      &::after
        @include after-frame(0px, 0px, 0px, 0px, 250px, 250px)
        @media(max-width: 630px)
          @include after-frame(0px, 0vw, 0px, 0px, 25vw, 25vw)
          border: 3vw solid #e8c300
      &-picture
        float: left
        width: 475px
        height: 475px
        // margin: 0px 120px 0 0
        @media(max-width: 630px)
          height: 47vw
          width: 47vw

    &-buttons
      display: flex
      justify-content: center
      margin: 5px 0 0 0

      &-button
        width: 563.58px
        height: 60px
        border-radius: 5px
        font-family: Open Sans
        font-style: normal
        font-weight: bold
        font-size: 12px
        line-height: 20px
        @media(max-width: 680px )
          width: auto
          min-width: 80vw
          height: 80px
          border-radius: 15px
          font-size: 16px

    &-info
      font: 13.2px/17px OpenSans-Regular,
      font-family: Open Sans
      color: rgba(51, 51, 51, 0.5)
      max-width: 550px

      width: 80vw
