.private-events
  &__container
    background-image: url('../images/privateEvents-bg.jpg')
    background-repeat: no-repeat
    background-size: cover
    max-width: 1440px
    display: flex

    flex-direction: column
    flex-wrap: wrap
    align-content: stretch
    justify-content: space-around
    padding: 50px 0 0px 0
    @media(min-width: 1023px) and (max-width: 1025px)
      display: block
    @media(max-width: 1024px)
      flex-wrap: nowrap

  &__title
    display: flex
    align-items: center
    flex-direction: column
    padding: 20px 0 20px 0
    // margin: 46px 0 -15px 0
    &-text
      font-family: Open Sans
      font-style: normal
      font-weight: bold
      font-size: 12px
      line-height: 14px
      display: flex
      align-items: center
      text-align: center
      letter-spacing: 0.6em
      text-transform: uppercase
  &__content
    display: flex
    justify-content: space-around
    flex-wrap: wrap
    padding: 20px 0 20px 0

    &-wrapper
      display: flex
      align-items: center
      @media(min-width: 632px)
        margin: 0px 0px 0 120px
      @media(max-width: 1214px)
        padding: 30px 0 30px 0
      &::after
        @include after-frame(0px, 350px, 0px, -590px, 250px, 250px)
        position: relative
        @media(max-width: 631px)
          @include after-frame( 0, 53vw, 0, -95vw, 30vw, 30vw)
          position: relative

        // @media(max-width: 768px)
        //   @include after-frame(0px,0vw, 0px, 0px, 25vw, 25vw)
        //     position: relative

        //   border: 15px solid #fde984

    &-wrapper-corp
      display: flex
      align-items: center
      @media(max-width: 1214px)
        padding: 30px 0 30px 0
      &::after
        @include after-frame(0px, 0px, 0px, -130px, 250px, 250px)
        // left: -16%
        position: relative
        @media(max-width: 631px)
          @include after-frame(0px, 0px, 0px, -15vw, 30vw, 30vw)
          position: relative

    &-weddings
      display: flex
      // position: relative
      background-image: url('..//images/privateEvents-bg__photo-left.png')
      background-repeat: no-repeat
      // background-size: cover
      height: 475px
      width: 475px
      flex-direction: row
      align-items: center
      justify-content: flex-end
      // background-size: 110%
      align-items: stretch
      @media(max-width: 631px)
        height: 80vw
        width: 80vw
    &-corporate
      display: flex
      background-image: url('..//images/privateEvents-bg__photo-right.png')
      background-repeat: no-repeat
      // background-size: cover
      background-size: 110%
      height: 475px
      width: 475px
      flex-direction: row
      align-items: center
      justify-content: flex-start
      align-items: stretch
      @media(max-width: 631px)
        height: 80vw
        width: 80vw

      // &::after
      //   @include after-frame(0px, 0px, 0px, 0px, 250px, 250px)
      //   display: flex
    &-link

      &-corp
        display: flex
        color: white
        width: 80px
        // height: 477px
        margin: 0px 0 0px -2px
        border-radius: 5px 0 0 5px
        background: black
        font-family: Banny
        font-size: 24px
        line-height: 12px
        text-indent: 20px
        text-transform: uppercase
        color: #FFFFFF
        align-items: center
        writing-mode: vertical-rl
        @media(max-width: 500px)
          font-size: 9vw
      &-wed
        display: flex
        color: white
        width: 80px
        // height: 477px
        margin: -1px -3px 0 0
        border-radius: 0px 5px 5px 0px
        background: black
        font-family: Banny
        font-size: 24px
        line-height: 12px
        text-indent: 20px
        text-transform: uppercase
        color: #FFFFFF
        align-items: center
        writing-mode: vertical-rl
        @media(max-width: 500px)
          font-size: 9vw
  &__info
    display: flex
    justify-content: center
    font-family: Open Sans
    font-style: normal
    font-weight: normal
    font-size: 12px
    line-height: 12px
    letter-spacing: 0.03em
    padding: 30px 0 50px 0
    @media(max-width: 520px)
      flex-direction: column
