.contact
  &__container
    display: flex
    flex-direction: column
    justify-content: center
    flex-wrap: wrap
    align-content: stretch
    align-items: center
    max-width: 1280px
    padding: 130px 0 100px 0
    @media(min-width: 1023px) and (max-width: 1025px)
      display: block
    @media(max-width: 680px)
      padding: 30px 0 30px 0

  &__content
    display: flex
    flex-direction: column
    align-items: center

    &-title
      // margin: 215px 0 38px 0
      display: flex
      justify-content: center
      align-self: center
      font: 36px/48px Banny,
      text-align: center
      text-transform: uppercase
      height: 60px
      padding: 0 0 12px 0
      &::after
        @include after-plank( 53px, 0, 0, 0px, 60px, 5.11px)
    &-subtitle
      padding: 12px 0 25px 0
      display: flex
      justify-content: center
      align-items: flex-end
      font: 21px/28px Open Sans
      font-style: normal
      font-weight: bold
      text-align: center
      max-width: 555px
      width: auto
      // max-height: 52px
      height: auto
      @media(max-width: 680px)
        padding: 12px 0 0px 0

    &-forms
      display: flex
      justify-content: center
      padding: 25px 0 0 0
      @media(max-width: 680px)
        padding: 10px 0 0px 0
      &-input
        width: 420px
        height: 60px
        background: #E5E5E5
        border-radius: 5px
        margin: 5px
        border: none
        outline: none
        font: 15px/19px OpenSans-Regular,
        padding: 7px 15px 5px 18px
        &:invalid
          border: 0.3px solid #dacaca00
        &:valid
          border: 2px solid yellow
        @media(max-width: 1440px)
          width: 30vw
          height: 60px
        @media(max-width: 680px)
          width: auto
          min-width: 80vw
          height: 75px
          border-radius: 15px
          font-size: 16px
      &-message
        width: 1280px
        height: 120px
        background: #E5E5E5
        border-radius: 5px
        margin: 5px
        border: none
        outline: none
        font: 15px/19px OpenSans-Regular,
        // padding: 7px 15px 5px 18px
        padding: 10px 0px 0px 15px
        @media(max-width: 1440px)
          width: 93vw
          height: 120px

        @media(max-width: 680px)
          width: 80vw
          border-radius: 15px
          padding: 10px 0px 0px 20px
      &-form
        display: flex
        flex-direction: row
        justify-content: space-between
        @media(max-width: 680px)
          flex-direction: column
    &-wrapper
      display: flex
      justify-content: space-between
      font: 12px/12px Open Sans,
      font-style: normal
      font-weight: 400
      // letter-spacing: .03em
      color: rgba(51, 51, 51, 0.5)
      flex-direction: row
      align-items: center
      max-width: 1280px
      @media(max-width: 1440px)
        width: 93vw
      @media(max-width: 680px)
        flex-direction: column
        width: 80vw

      &-info
        display: flex
        flex-wrap: wrap
        justify-content: flex-start
        align-items: center
        margin: 0 30px 0px -10px
        @media(max-width: 680px)
          // flex-direction: column
          padding: 20px 0 20px 0
          margin: 0 0px 0px 0px
        &-wr
          display: flex
          margin: 10px 0 10px 0
          // width: 200px

          order: 2
      &-button
        width: 640px
        height: 60px
        background: #E8C300
        border-radius: 5px
        font-family: Open Sans
        font-style: normal
        font-weight: bold
        font-size: 12px
        line-height: 20px
        text-transform: uppercase
        align-items: center
        text-align: center
        @media(max-width: 1440px)
          width: 60vw
        @media(max-width: 680px)
          width: 80vw
          height: 60px
          border-radius: 15px
          padding: 10px 0 10px 0

textarea
  resize: none
.fa-envelope
  background-image: url('/assets/images/message.svg')
  background-repeat: no-repeat
  width: 15px
  height: 15px
.fa-map-marker-alt
  background-image: url('/assets/images/map-marker-alt-solid.svg')
  background-repeat: no-repeat
  height: 15px
  width: 15px
.fa-phone
  background-image: url('/assets/images/phone-solid.svg')
  background-repeat: no-repeat
  width: 15px
  height: 15px
.fas
  margin: 0 10px 0px 30px
