
.photo-galerie
  &__container
    display: flex
    flex-direction: column
    max-width: 1440px
    margin: -155px 0 -250px 0
    @media(max-width: 1327px)
      margin: -220px 0 -250px 0
    @media(max-width: 768px)
      margin: 0px 0 -250px 0

  &-images
    margin: 20px 0 0 0
    @media(max-width: 768px)
  &__slider
    margin: 0 0px 0 0
  &__slider-wrapper
    max-width: 1440px
  &__slider-item
    flex: 0 0 100%
    max-width: 100%
    height: 360px
    min-height: 250px
    display: flex
    justify-content: center
    align-items: center
    color: rgba(255, 255, 255, 0.8)
    font-size: 7rem
    @media (min-width: 768px)
      flex: 0 0 25%
      max-width: 100%
      height: 360px

      // height: auto
    @media (min-width: 1200px)
      flex: 0 0 33.3333333333%
      max-width: 33.3333333333%
      height: 360px

    @media (min-width: 1200px)
      flex: 0 0 10.3333333333%
      max-width: 25%
