.specialties
  &__container
    display: flex
    // flex-direction: row
    justify-content: center
    align-content: stretch
    flex-wrap: wrap
    background-size: cover
    max-width: 1440px
    background-image: url(../images/privateEvents-bg.jpg)
    background-repeat: no-repeat
    // height: 677px
    @media(max-width: 1024px)
      // min-height: 720px

    // @media(min-width: 1025px)
    //   max-height: 675px
    //   height: 675px
    @media(min-width: 1023px) and (max-width: 1025px)
      display: block
  &__content
    width: 90vw
    display: flex
    justify-content: space-evenly
    flex-wrap: wrap
    align-items: center
    // @media(max-width: 1024px)
    //   flex-direction: column
    //   padding: 0 0 50px 0
    &-title-caption
      display: flex
      justify-content: center
      font-family: Open Sans
      font-style: normal
      font-weight: bold
      font-size: 12px
      line-height: 14px
      letter-spacing: 0.55em
      @media(min-width: 500px)
        padding: 50px 0 0px 0
      @media(min-width: 200px)
        padding: 50px 0 30px 0

    &-text
      // padding: 0 0 0 40px 

      display: flex
      justify-content: space-between
      flex-direction: column
      max-width: 600px
      min-height: 240px
      max-height: 241px
      // padding: 0 10px 0 10px
      @media(max-width: 1024px)
        width: 80vw

      &-title
        display: flex
        font: 36px/48px Banny,
        justify-content: center
        // height: 120px

        &::after
          @include after-plank( 57px, 0, 0, 0px, 60px, 5.11px)
          // @media(max-width: 1125px)
            // margin: 95px 0 0 0
          @media(max-width: 1024px)
            margin: 57px 0 0 0
          @media(max-width: 522px)
            margin: 95px 0 0 0
        @media(max-width: 1024px)
          padding: 20px 0 20px 0
      &-subtitle
        display: flex
        font: bold 18px/28px 'OpenSansBold', sans-serif
        font-weight: 700
        padding: 15px 0 0 0

      &-description
        display: flex
        font: 15px/24px OpenSans-Regular,sans-serif
        @media(max-width:1024px )
          padding: 30px 0 0 0

    &-image
      display: flex
      justify-content: center
      align-content: center
      flex-direction: column
      align-content: flex-start
      align-items: flex-start
      flex-wrap: wrap
      max-width: 642px
      width: auto
      @media(max-width: 630px)
        max-width: 60vw
      &::after
        @include after-frame(0px, 0px, 0px, 0px, 250px, 250px)
        @media(max-width: 630px)
          @include after-frame(0px, 0vw, 0px, 0px, 25vw, 25vw)
          border: 3vw solid #e8c300
      &-picture
        float: left
        width: 475px
        height: 475px
        margin: 0px 0 0 120px
        @media(max-width: 630px)
          margin: 0px 0 0 10vw
          height: 47vw
          width: 47vw

.slider
  // * chiefSlider
  // *   site: https://itchief.ru/javascript/slider
  // *   github: https://github.com/itchief/ui-components
  // *
  // * Copyright 2018-2021 Alexander Maltsev
  // * Licensed under MIT (https://github.com/itchief/ui-components/blob/master/LICENSE)
  position: relative
  &__container
    overflow: hidden
    max-width: 1440px
    margin: 0 auto
    width: -webkit-fill-available
        // &__wrapper
    //   overflow: hidden
  &__items
    display: flex
    transition: transform 0.5s ease
  &_disable-transition
    transition: none
  &__item
    flex: 0 0 100%
    max-width: 100%
    // flex: 0 0 50%
    // max-width: 50%
    min-height: 675px
    display: flex
    justify-content: center
    align-items: center
    color: rgba(255, 255, 255, 0.8)
    font-size: 7rem
    user-select: none
    @media (max-width: 1327px)
      min-height: 800px

    @media (max-width: 768px)
      display: flex
      flex-direction: column
      justify-content: flex-start
    @media (max-width: 630px)
      min-height: 675px

    // @media (max-width: 1200px)
      // flex: 0 0 50%
      // max-width: 50%

  &__control
    position: absolute
    top: 50%
    display: flex
    align-items: center
    justify-content: center
    width: 40px
    color: #fff
    text-align: center
    height: 50px
    transform: translateY(-50%)
    background: rgba(0, 0, 0, 0.2)
  &__control_hide
    display: none
  &__control[data-slide='prev']
    left: 0
  &__control[data-slide='next']
    right: 0
  &__control:hover,
  &__control:focus
    color: #fff
    text-decoration: none
    outline: 0
    background: rgba(0, 0, 0, 0.3)
  &__control::before
    content: ''
    display: inline-block
    width: 20px
    height: 20px
    background: transparent no-repeat center center
    background-size: 100% 100%
  &__control[data-slide='prev']::before
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E")
  &__control[data-slide='next']::before
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E")
  &__indicators
    position: absolute
    right: 0
    bottom: 0
    left: 0
    z-index: 15
    display: flex
    justify-content: center
    padding-left: 0
    margin-right: 15%
    margin-left: 15%
    list-style: none
    margin-top: 0
    margin-bottom: 0
    padding: 20px 0 20px 0
    @media (max-width: 1024px)
      position: relative
      padding: 20px 0 20px 0

    @media (min-width: 1024px)
      // bottom: 30pxbotto
      padding: 20px 0 20px 0

  &__indicators li
    box-sizing: content-box
    flex: 0 1 auto
    width: 10px
    height: 10px
    border-radius: 10px
    margin-right: 10px
    margin-left: 10px
    text-indent: -999px
    cursor: pointer
    background: rgba(255, 255, 255, 0.5)
    background-clip: padding-box
    // border-top: 15px solid transparent
    // border-bottom: 15px solid transparent
  &__indicators li.active
    background-color: rgba(255, 255, 255, 0.9)
  &__wrapper
    overflow: hidden
