.delicious
  &__container
    display: flex
    flex-direction: column
    justify-content: center
    flex-wrap: wrap
    align-content: stretch
    align-items: center
    max-width: 1440px
    padding: 100px 0 50px 0
    // @media(min-width: 1025px)
    //   max-height: 675px
    //   height: 675px
    @media(min-width: 1023px) and (max-width: 1025px)
      display: block
    @media(max-width: 1024px)
      flex-wrap: nowrap
      height: 100%
      width: 100%
    // height: 920px
  &__heading
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-content: center
    align-items: center
    max-width: 1440px

    width: 90vw
    min-height: 137px
    padding: 0 0 50px 0

    // margin: 0px 0 50px 0
    &-title
      display: flex
      justify-content: center
      font-family: Banny
      font-size: 36px
      line-height: 48px
      text-align: center
      text-transform: uppercase
      // padding: 0 0 15px 0
      letter-spacing: 2.5px
      height: 85px
      &::after
        @include after-plank( 56px, 0, 0, 0px, 60px, 5.11px)
        @media(max-width: 333px )
          margin-top: 100px
      @media(max-width: 333px )
        height: 120px

    &-subtitle
      display: flex
      letter-spacing: 1px
      font-family: Open Sans
      font-style: normal
      font-weight: bold
      font-size: 19px
      line-height: 28px
        // padding: 20px 0 0 0

  &__content
    display: flex
    justify-content: center
    max-width: 1440px
  &__dish
    &-main
      display: flex
      padding: 0px 10px 0px 10px
      font: 18px/18px Banny,
      width: 400px
      height: 35px
      color: black
      justify-content: space-around
      @media( max-width: 1200px  )
        font: 26px/29px Banny,
        max-width: 750px
        width: 80vw
        height: 40px
        justify-content: space-between
      @media( max-width: 500px  )
        font: 26px/29px Banny,
        width: 90vw
        height: 60px
        justify-content: space-between

    &-title
      display: flex
      flex-direction: column
      align-items: flex-start
      align-items: center
      align-content: center
      min-width: 125px
      max-width: 260x
      // padding: 0 10px 0 0
    &-subtitle
      font: 12px/18px OpenSans-Regular,
      color: black
      opacity: 0.6
  &__cost
    display: flex
    min-width: 125px
    max-width: 125px
    justify-content: center
    @media(max-width: 500px)
      min-width: 50px

  &__check-body
    display: flex
  &__input
    display: flex
    // padding: 15px 48.50px 15px 48.50px
    max-width: 1280px
    justify-content: center
    padding: 15px 0 15px 0
    // min-width: 1vw
    // @media(max-width: 300px)
    // width: 90vw
    @media(max-width: 607px)
      height: 120px
    @media(max-width: 300px)
      height: 150px
input:checked ~ label .delicious__dish-main
  font-weight: bold
  @media(max-width: 1200px)
    color: black
    font: 26px/29px Banny,
    font-weight: bold
  // width: 640px

.tabs
  // border: 1px solid lightgray
  display: flex
  flex-direction: column
  align-items: center
  margin: 0 0 0 0
  &__nav
    display: flex
    flex-wrap: wrap
    list-style-type: none
    // background: #f8f8f8
    // margin: 0 263px 0 263px
    padding: 0 15px 0 15px
  &__content

    margin: 36px 0 0 0
    max-width: 1440px
    // width: 90vw
  &__link
    padding: 0.5rem 0.75rem
    font-family: Open Sans
    font-style: normal
    font-weight: bold
    font-size: 13px
    line-height: 18px
    color: black
    flex-shrink: 0
    flex-grow: 1
    width: 120px
    height: 50px
    display: flex
    flex-direction: column
    flex-wrap: nowrap
    align-content: center
    justify-content: center
    align-items: stretch
    text-transform: uppercase
    border-right: 1px solid rgba(51, 51, 51, 0.2)
    &:nth-last-of-type(1)
      @media(min-width: 510px)
        border-right: 0px
    @media(max-width: 509px)
      border: 1px solid rgba(51, 51, 51, 0.05)
      border-radius: 10px

    // &::after
    //   position: absolute
    //   content: " "
    //   margin: 0px 0 0 0px
    //   border-left: 1px solid rgba(51, 51, 51, 0.2)
    //   width: 0px
    //   height: 40px
  &__link:hover:not(.tabs__link_active)
    background-color: rgba(63, 60, 60, 0.055)
    border-radius: 4px
  &__link_active
    opacity: 0.7
    background: lightgray
    cursor: default
    border-radius: 4px
    // &:not(.tabs__link_active):hover,
    // &:not(.tabs__link_active):focus
    //     background-color: #efefef
  &__content
    // padding: 1rem
  &__pane
    display: none
    &_show
      display: block
  &__table-tr
    display: flex
    @media(max-width: 1200px)
      flex-direction: column
