
.team
  &__container
    background-image: url(../images/specialty-bg.jpg)
    background-repeat: no-repeat
    background-size: cover
    max-width: 1440px
    display: flex
    flex-direction: column
    justify-content: center
    flex-wrap: wrap
    align-content: stretch

    @media(min-width: 1023px) and (max-width: 1025px)
      display: block
    @media(max-width: 1024px)
      flex-wrap: nowrap
      height: 100%
      width: 100%
      background-image: url(../images/specialty-bg-phone.jpg)

  &__content
    display: flex
    justify-content: space-evenly
    flex-direction: row
    align-items: center
    padding: 50px 0 50px 0

    @media(max-width: 1024px)
      flex-direction: column
      padding: 0 0 50px 0
    &-subtitle
      display: flex
      justify-content: center
      font-family: Open Sans
      font-style: normal
      font-weight: bold
      font-size: 12px
      line-height: 14px
      letter-spacing: 0.5em
      padding: 50px 0 30px 0

      // @media(max-width: 1024px)
      //   padding: 50px 0 50px 0

    &-text
      display: flex
      justify-content: space-around
      flex-direction: column
      max-width: 590px
      min-height: 240px
      // width: 90vw
      &-title
        font: 36px/48px Banny,
        display: flex
        height: 65px
        padding: 10px 0 10px 0

        justify-content: center
        &::after
          @include after-plank( 50px, 0, 0, 0px, 60px, 5.11px)
      @media(max-width: 1024px)
        // padding: 0 0 10px 0
        max-width: 590px
        width: 90vw
      &-undertitle
        font: bold 18px/28px 'OpenSansBold', sans-serif
        @media(max-width: 1024px)
          padding: 10px 0 10px 0

      &-description
        font: 15px/24px OpenSans-Regular,sans-serif
        @media(max-width: 1024px)

          padding: 10px 10px 0 10px

    &-image
      display: flex
      justify-content: center
      align-content: center
      flex-direction: column
      align-content: flex-start
      align-items: flex-start
      flex-wrap: wrap
      max-width: 642px
      width: auto
      @media(max-width: 630px)
        max-width: 60vw
      &::after
        @include after-frame(0px, 0px, 0px, 0px, 250px, 250px)
        @media(max-width: 630px)
          @include after-frame(0px, 0vw, 0px, 0px, 25vw, 25vw)
          border: 3vw solid #e8c300
      &-picture
        float: left
        width: 475px
        height: 475px
        margin: 0px 0 0 120px
        @media(max-width: 630px)
          margin: 0px 0 0 10vw
          height: 47vw
          width: 47vw
